import { Injectable } from '@angular/core';

@Injectable()
export class UserValidateService {

  constructor() { }
   
  /**
  * Validate email
  *
  * @param {string} email
  *
  * @returns {bool}
  */ 
  validateEmail(email) {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    
    return re.test(email);
  } 
}



